  display: block;
  text-align: center;
  width: 300px;
  margin: auto;
  margin-top: 15%;
  padding: 1.5%;
  background-color: #98c1d9;
  border-radius: 10px;
}
label,
.registerBtn {
  display: block;
  width: 250px;
  margin: auto;
